nav {
    position: relative;
    display: flex;
    width: 100%;
}

nav .start, nav .end {
    flex-grow: 1;
    display:flex;
    padding: 10px;
}

nav .end {
    justify-content: end;
}

nav button {
    padding: 5px;
    margin-left: 5px;
}

#userSettings {
    display: none;
    position: absolute;
    top: 100%;
    left: 100%;
    transform: translateX(-100%);
}