.panelWrapper h1 {
    text-align: center;
}

.panelWrapper .panel {
    width: 250px;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.panelWrapper .panel input {
    max-width: 100%;
    padding: 5px;
    margin-bottom: 5px;
}

.panelWrapper .panel button {
    max-width: 100%;
    margin: 5px;
}